*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  width: 100%;
  min-height: 100vh;
  letter-spacing: .02em;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  font-family: Playfair Display, serif;
  font-size: 14px;
  line-height: 1.85;
  transition: all 1.5s;
  display: flex;
  overflow-x: hidden;
}

main {
  width: 100%;
  min-height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: Playfair Display, serif;
  display: flex;
}

body::-webkit-scrollbar {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  width: 5px;
  background: #000;
}

body::-webkit-scrollbar-thumb {
  background: #fff;
  border-radius: 5px;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

img {
  height: auto;
  width: 100%;
  display: block;
}

p, h1, h2, h3, h4 {
  margin: 0 auto;
}

ul, ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}

.example {
  color: #2f2f37;
  max-width: 960px;
  background-color: #fff;
  padding: 16px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.example code {
  color: #fff;
  background-color: #ff6b0a;
  border-radius: 2px;
  padding: 2px 6px;
  font-family: monospace;
  font-size: 16px;
  font-weight: 700;
}

.example .background {
  height: 240px;
  background-image: url("logo.268195b3.png");
  background-position: center;
  background-size: cover;
}

header {
  flex-direction: column;
  display: flex;
}

.section {
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 0 50px;
  font-family: Playfair Display, serif;
  display: flex;
  overflow: hidden;
}

.section-header {
  width: 100%;
  max-width: 950px;
  text-align: center;
  margin: 0 auto;
}

.section-suptitle {
  color: #333;
  font-family: Playfair Display;
  font-size: 24px;
  font-style: italic;
}

.section-title {
  color: #333;
  text-transform: uppercase;
  font-size: 30px;
  font-weight: 700;
}

.section-title:after {
  content: "";
  width: 60px;
  height: 3px;
  background-color: #607b83;
  margin: 30px auto;
  display: block;
}

.section-text p {
  color: #999;
  text-align: center;
  margin-top: 0;
  margin-bottom: 80px;
  font-size: 15px;
}

.hero1 {
  width: 100%;
  height: 100vh;
  background-image: url("hero1.2b879456.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

.hero2 {
  width: 100%;
  height: 450px;
  background-image: url("octopus.084024d9.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

.hero3 {
  width: 100%;
  height: 100vh;
  background-image: url("ocean1.60c07a87.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  position: relative;
  top: 0;
  left: 0;
  overflow: auto;
}

.save {
  max-width: 650px;
  color: #fff;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  flex-wrap: wrap;
  justify-content: center;
  padding: 100px 50px 0;
  font-size: 100px;
  font-weight: 900;
  line-height: 1;
  display: flex;
}

.save span {
  font-size: 180px;
  font-style: italic;
  font-weight: 900;
}

.save__text {
  max-width: 1000px;
  color: #fff;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  text-align: center;
  margin-top: 300px;
  font-size: 18px;
  display: block;
}

.save__text1 {
  max-width: 1000px;
  color: #fff;
  text-align: center;
  background-color: rgba(0, 70, 110, .2);
  padding: 50px;
  font-size: 18px;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.nav {
  text-transform: uppercase;
  font-size: 14px;
}

.nav-link {
  vertical-align: top;
  color: #fff;
  margin: 0 15px;
  text-decoration: none;
  transition: color .1s linear;
  display: inline-block;
  position: relative;
}

.nav-link:after {
  content: "";
  width: 100%;
  height: 3px;
  opacity: 0;
  z-index: 1;
  background-color: #fce38a;
  transition: opacity .1s linear;
  display: block;
  position: absolute;
  top: 100%;
  left: 0;
}

.nav-link:hover {
  color: #fce38a;
}

.nav-link:hover:after, .nav-link.active:after {
  opacity: 1;
}

.nav-link.active {
  color: #fce38a;
}

.header {
  width: 100%;
  z-index: 1000;
  padding-top: 30px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.header.fixed {
  width: 100%;
  z-index: 999;
  color: #fff;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: rgba(0, 70, 110, .1);
  gap: 70px;
  padding: 10px 50px;
  transition: all .3s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: 0 3px 10px rgba(0, 0, 0, .2);
}

.header.active {
  background-color: #eb8b8d;
}

.header-inner {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-logo {
  color: #fff;
  cursor: pointer;
  font-size: 30px;
  font-style: italic;
  font-weight: 700;
}

.nav-toggle {
  width: 30px;
  z-index: 1;
  cursor: pointer;
  background: none;
  border: 0;
  outline: none;
  padding: 10px 0;
  display: none;
  position: absolute;
  top: 25px;
  right: 25px;
}

.nav-toggle-item {
  width: 100%;
  height: 3px;
  background-color: #fff;
  margin: auto;
  transition: background .2s linear;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav-toggle.active .nav-toggle-item {
  background: none;
}

.nav-toggle.active .nav-toggle-item:before {
  transform-origin: 0 0;
  transform: rotate(45deg)translateX(-2px);
}

.nav-toggle.active .nav-toggle-item:after {
  transform-origin: 0 100%;
  transform: rotate(-45deg)translateX(-3px);
}

.nav-toggle-item:before, .nav-toggle-item:after {
  width: 100%;
  height: 3px;
  content: "";
  z-index: 1;
  background-color: #fff;
  transition: transform .2s linear;
  position: absolute;
  left: 0;
}

.nav-toggle-item:before {
  top: -8px;
}

.nav-toggle-item:after {
  bottom: -8px;
}

.burger {
  cursor: pointer;
  background: none;
  border: 0;
  padding: 20px 10px;
  display: none;
}

.burger__item {
  width: 30px;
  height: 3px;
  color: rgba(0, 0, 0, 0);
  background-color: #fff;
  font-size: 0;
  display: block;
  position: relative;
}

body.lb-disable-scrolling {
  overflow: hidden;
}

.lightboxOverlay {
  z-index: 9999;
  filter: progid:DXImageTransform. Microsoft. Alpha(Opacity= 80);
  opacity: .8;
  background-color: #000;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
}

.lightbox {
  width: 100%;
  z-index: 10000;
  text-align: center;
  outline: none;
  font-weight: normal;
  line-height: 0;
  position: absolute;
  left: 0;
}

.lightbox .lb-image {
  height: auto;
  max-width: inherit;
  max-height: none;
  border: 4px solid #fff;
  border-radius: 3px;
  display: block;
}

.lightbox .gallery__link img {
  border: none;
}

.lb-outerContainer {
  zoom: 1;
  width: 250px;
  height: 250px;
  background-color: #fff;
  border-radius: 4px;
  margin: 0 auto;
  position: relative;
}

.lb-outerContainer:after {
  content: "";
  clear: both;
  display: table;
}

.lb-loader {
  height: 25%;
  width: 100%;
  text-align: center;
  line-height: 0;
  position: absolute;
  top: 43%;
  left: 0;
}

.lb-cancel {
  width: 32px;
  height: 32px;
  background: url("loading.46e1ff58.gif") no-repeat;
  margin: 0 auto;
  display: block;
}

.lb-nav {
  height: 100%;
  width: 100%;
  z-index: 10;
  position: absolute;
  top: 0;
  left: 0;
}

.lb-container > .nav {
  left: 0;
}

.lb-nav .gallery__link {
  background-image: url("data:image/gif;base64,R0lGODlhAQABAPAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw==");
  outline: none;
}

.lb-prev, .lb-next {
  height: 100%;
  cursor: pointer;
  display: block;
}

.lb-nav .gallery__link.lb-prev:hover, .lb-nav .gallery__link.lb-next:hover {
  filter: progid:DXImageTransform. Microsoft. Alpha(Opacity= 100);
  opacity: 1;
}

.lb-dataContainer {
  zoom: 1;
  width: 100%;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  margin: 0 auto;
  padding-top: 5px;
}

.lb-dataContainer:after {
  content: "";
  clear: both;
  display: table;
}

.lb-data {
  color: #ccc;
  padding: 0 4px;
}

.lb-data .lb-details {
  width: 85%;
  float: left;
  text-align: left;
  line-height: 1.1em;
}

.lb-data .lb-caption {
  font-size: 13px;
  font-weight: bold;
  line-height: 1em;
}

.lb-data .lb-caption .gallery__link {
  color: #4ae;
}

.lb-data .lb-number {
  clear: left;
  color: #999;
  padding-bottom: 1em;
  font-size: 12px;
  display: block;
}

.lb-data .lb-close {
  float: right;
  width: 30px;
  height: 30px;
  text-align: right;
  filter: progid:DXImageTransform. Microsoft. Alpha(Opacity= 70);
  opacity: .7;
  -o-transition: opacity .2s;
  background: url("close.e6e826de.png") 100% 0 / cover no-repeat;
  outline: none;
  transition: opacity .2s;
  display: block;
}

.lb-data .lb-close:hover {
  cursor: pointer;
  filter: progid:DXImageTransform. Microsoft. Alpha(Opacity= 100);
  opacity: 1;
}

.lb-prev, .lb-next {
  width: 40px;
  height: 40px;
  text-align: center;
  color: #ccc;
  cursor: pointer;
  font-size: 1.4em;
  transition: all .3s ease-in;
  display: block;
  position: absolute;
  top: 50%;
}

.lb-prev {
  opacity: .7;
  background: url("prev.dcd580d6.png") center / cover no-repeat;
  outline: none;
  transition: opacity .2s;
  left: 2em;
}

.lb-next {
  opacity: .7;
  background: url("next.86169bb8.png") center / cover no-repeat;
  outline: none;
  transition: opacity .2s;
  right: 2em;
}

.gallery-wrapper {
  width: 100%;
  height: 100%;
  background-color: #607b83;
}

.gallery img {
  min-width: 100%;
  height: 100%;
  display: block;
}

.gallery {
  grid-gap: 2px;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  display: grid;
}

.gallery__link {
  background: #fff;
  position: relative;
  box-shadow: 0 5px 10px rgba(0, 0, 0, .1), 0 20px 20px rgba(0, 0, 0, .05);
}

.gallery__content {
  text-align: center;
  width: 100%;
  height: 100%;
  z-index: 100;
  opacity: 0;
  background-color: rgba(0, 70, 110, .7);
  flex-direction: column;
  justify-content: center;
  transition: opacity .2s linear;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.gallery__link:hover .gallery__content {
  opacity: 1;
}

.gallery__title {
  text-transform: uppercase;
  color: #fff;
  margin-bottom: 5px;
  font-size: 14px;
  font-weight: 700;
}

.gallery__text {
  width: 200px;
  color: #fff;
  font-size: 14px;
  display: block;
}

.anchor {
  position: relative;
}

.anchor__link {
  display: block;
  position: absolute;
  top: -100px;
}

.reviews {
