}

.reviews-photo {
  background-image: url("montale.46397bc8.jpg");
}

.reviews-photo1 {
  background-image: url("cousteau.3730929d.webp");
}

.reviews-photo2 {
  background-image: url("wyland.b1dcab7c.jpg");
}

.reviews-photo3 {
  background-image: url("carson.334914b9.jpg");
}

.reviews-photo4 {
  background-image: url("wordsworth.f4f718e4.jpg");
}

.reviews-photo .reviews-text {
  color: #999;
  margin-bottom: 10px;
  margin-left: 30px;
  font-family: inherit;
  font-size: 24px;
  font-style: italic;
  font-weight: 300;
}

.reviews-author {
  color: #333;
  margin-left: 30px;
  font-family: inherit;
  font-size: 24px;
  font-style: italic;
}

.reviews-author:before {
  content: "";
  vertical-align: middle;
  width: 60px;
  height: 3px;
  background-color: #607b83;
  margin-right: 10px;
  display: inline-block;
}

.reviews-text {
  color: #999;
  margin-bottom: 10px;
  margin-left: 30px;
  font-size: 24px;
  font-style: italic;
  font-weight: 300;
}

.reviews-btn {
  color: rgba(0, 0, 0, 0);
  z-index: 1;
  font-size: 0;
  position: absolute;
  top: 50%;
