}

.section.organization {
  padding: 0;
}

.organization-wrapper {
  width: 100%;
  background-color: #607b83;
  justify-content: center;
  align-items: center;
  padding: 40px 0;
  display: flex;
}

.org__img {
  width: 190px;
  height: 190px;
  display: block;
}

.organization {
  text-align: center;
}

.organization__list {
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 30px;
  display: flex;
}

.organization__item {
  width: calc(50% - 15px);
}

@media (min-width: 768px) {
  .organization__item {
    width: calc(33.3333% - 20px);
  }
}

@media (min-width: 1200px) {
  .organization__item {
    width: calc(16.6667% - 25px);
  }
}

.organization__link {
  color: #fff;
  border-radius: 4px;
  align-items: center;
  margin: 0;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), border .25s cubic-bezier(.4, 0, .2, 1), scale .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  transform: scale(1);
}

.organization__link:hover, .organization__link:focus {
  color: #fce38a;
  transform: scale(1.2);
}

.organization__icon {
  width: 100%;
  fill: currentColor;
  height: 100px;
  display: block;
}

.section.video-play {
  padding: 50px 0;
}

.wrapper {
  width: 100%;
  color: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: .75rem;
  line-height: 1.67;
  display: flex;
  position: relative;
}

.full-screen {
  min-height: 100vh;
  width: 100%;
  flex-direction: column;
  display: flex;
  position: relative;
}

.full-screen__body {
  z-index: 2;
  background-color: rgba(0, 0, 0, .3);
  flex-direction: column;
  flex: auto;
  justify-content: center;
  align-items: center;
  padding: 50px 15px;
  display: flex;
  position: relative;
}

.full-screen__title {
  font-size: 50px;
}

.full-screen__video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
}

.full-screen__title {
  width: 1000px;
  color: #fff;
  text-align: center;
  font-size: 120px;
  font-weight: 900;
  display: inline-block;
}

.full-screen__text {
  width: 1000px;
  color: #fff;
  text-align: center;
  padding: 50px;
  font-size: 24px;
  font-style: italic;
  display: inline-block;
}

@media (max-width: 1023px) {
  .save {
    text-align: center;
    padding-top: 150px;
    font-size: 80px;
  }

  .save span {
    font-size: 100px;
  }

  .save__text {
    max-width: 700px;
    margin-top: 200px;
    font-size: 14px;
    line-height: 1.5;
  }

  .save__text1 {
    max-width: 1200px;
    padding: 0;
    font-size: 14px;
    line-height: 1.5;
  }

  .hero2 {
    height: 350px;
  }

  .organization__list {
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: repeat(2, 1fr);
  }

  .footer {
    height: 480px;
  }
}

@media (max-width: 768px) {
  .nav {
    width: 100%;
    height: 100vh;
    background-color: #607b83;
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
  }

  .nav.active {
    display: block;
  }

  .nav-link {
    margin: 0;
    padding: 8px 20px;
    display: block;
  }

  .nav-link:after {
    display: none;
  }

  .nav-toggle {
    display: block;
  }

  .gallery {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  }

  .footer {
    height: 120px;
  }
}

@media (max-width: 480px) {
  .section-title {
    font-size: 24px;
  }

  .save {
    text-align: center;
    padding-top: 100px;
    font-size: 50px;
  }

  .save span {
    font-size: 90px;
